<template>
    <yd-layout>
        <div class="empAccept">
            <!-- <div class="fuyanInput">
                <div slot="left" class="leftText">填写附言</div>
                <trsTextarea v-model="fuyan" placeholder="请输入附言内容"></trsTextarea>
            </div> -->
            <div class="line" v-if="simSendReqActivity=='simSendReqActivity'" :class="{lineACtive:lineNum==1}" @click="clickInfo(1)">返回至申请人</div>
            <div class="line" v-if="simSecrataryAppActivity=='simSecrataryAppActivity'" :class="{lineACtive:lineNum==2}" @click="clickInfo(2)">送秘书预审{{secretaryName}}</div>
            <div class="line" v-if="simResultActivity=='simResultActivity'" :class="{lineACtive:lineNum==3}" @click="clickInfo(3);">完成受理</div>
        </div>
        <yd-popup v-model="finishShow" position="center" width="5.79rem">
            <div class="other">意见<span class="otherSpan" @click="finishShow=false">取消</span></div>
            <yd-cell-group class="otherInput">
                <yd-cell-item>
                    <yd-textarea slot="right" v-model="opinion" placeholder="请您输入意见..."></yd-textarea>
                </yd-cell-item>
            </yd-cell-group>
            <div class="otherBtn">
                <ul>
                    <li class="yes" @click="result=1;sendBtn()">同意</li>
                    <li class="no" @click="result=0;sendBtn()">不同意</li>
                </ul>
            </div>
        </yd-popup>
        <yd-popup v-model="statusPopup" position="bottom" height="4rem">
            <div class="confirmBtn" slot="top">
                <div class="leftBtn" v-on:click="statusPopup = false">取消</div>
                <div class="rightBtn" v-on:click="statusPopup = false;lineNum=2;">确定</div>
            </div>
            <div class="selectStatus">
                <ul class="ul">
                    <li class="li" v-for="(item, index) in secretariesList" :key="index" v-bind:class="{active:statusRadio==index}" v-on:click="statusRadio=index;leaderIDs=item.operatorid;secretaryName='（' + item.empname + '）'">{{item.empname}}
                    </li>
                </ul>
            </div>
        </yd-popup>
        <yd-tabbar slot="tabbar" v-show="statusPopup==false">
            <div class="agree" @click="$router.go(-1)">返回</div>
            <div class="back" @click="sendBtn()">确认发送</div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'incAccept',
    data() {
        return {
            lineNum: -1,
            statusPopup: false,
            statusRadio: -1,
            nextBtn: {},
            secretariesList: [],
            simSendReqIDs: '',
            simResultIDs: '',
            activityID: '',
            leaderIDs: '',
            secretaryName: '',
            simResultActivity: '',
            simSecrataryAppActivity: '',
            simSendReqActivity: '',
            finishShow: false,
            opinion: '',
            result: ''
            // fuyan: ''
        }
    },
    mounted() {
        let vm = this;
        vm.getData();
        vm.secretariesList = vm.getIncomeInfo.secretariesList;
    },
    computed: {
        ...mapGetters(['getUserInfo', 'getIncomeInfo'])
    },
    methods: {
        getData() {
            let vm = this;
            for (let key in vm.getIncomeInfo.jsonData.nextBtn) {
                if (key == 'simResultActivity') {
                    vm.simResultIDs = vm.getIncomeInfo.jsonData.nextBtn.simResultActivity[0];
                    vm.simResultActivity = key;
                } else if (key == 'simSecrataryAppActivity') {
                    vm.simSecrataryAppActivity = key;
                } else if (key == 'simSendReqActivity') {
                    vm.simSendReqIDs = vm.getIncomeInfo.jsonData.nextBtn.simSendReqActivity[0];
                    vm.simSendReqActivity = key;
                }
            }
        },
        clickInfo(num) {
            let vm = this;
            if (num == 1) {
                vm.lineNum = 1;
                vm.leaderIDs = vm.simSendReqIDs;
                vm.activityID = 'simSendReqActivity';
                vm.secretaryName = '';
                vm.statusRadio = -1;
            } else if (num == 2) {
                vm.leaderIDs = '';
                vm.statusPopup = true;
                vm.activityID = 'simSecrataryAppActivity';
            } else if (num == 3) {
                vm.finishShow = true;
                vm.lineNum = 3;
                vm.secretaryName = '';
                vm.statusRadio = -1;
                vm.leaderIDs = vm.simResultIDs;
                vm.activityID = 'simResultActivity';
            }
        },
        sendBtn() {
            let vm = this;
            if (vm.leaderIDs == '') {
                vm.$dialog.toast({
                    mes: '请选择发送人',
                    timeout: 2000
                });
            } else if (vm.activityID == 'simResultActivity' && vm.opinion == '') {
                vm.finishShow = true;
                vm.$dialog.toast({
                    mes: '请输入您的意见',
                    timeout: 2000
                });
            } else {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'crossSysSimAppr',
                    'workItemID': vm.$route.params.workId,
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'activityID': vm.activityID,
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': vm.opinion,
                    'workTitle': vm.getIncomeInfo.jsonData.reqdeptname + vm.getIncomeInfo.jsonData.requsername + '收入证明申请',
                    'ext': '{"result":"' + vm.result + '"}'
                }, function(res) {
                    vm.finishShow = false;
                    vm.$dialog.toast({
                        mes: '发送成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle');
                        }
                    });
                });
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import 'proofOfIncome.less';

.other {
    position: relative;

    .otherSpan {
        position: absolute;
        right: .2rem;
        color: #59a5fd;
        padding: 0 .2rem;
    }
}

.empAccept {
    min-height: 100%;
    background-color: #fff;

    .line {
        height: 1.1rem;
        line-height: 1.1rem;
        font-size: .32rem;
        color: #333;
        padding: 0 .45rem;
        border-bottom: 1px solid #eaeaea;
    }

    .lineACtive {
        background: url(../../../static/images/workCenter/20.png) no-repeat;
        // background-size: .4rem .3rem;
        background-position: 6.5rem center;
    }
}
</style>


